import React from "react";
import "../css/Contact.css";
import email from "../imagen/email.jpg";

function Contact() {
  return (
    <div className="titulo10">
      <img src={email} />
      <div className="formulario">
        <h1>Contacteme</h1>
        <form action="https://formspree.io/mgenavrv" method="POST">
          <input placeholder="Nombre y Apellido" type="text" name="name" />
          <input placeholder="E-mail" type="email" name="_replyto" />
          <textarea
            placeholder="Mensaje"
            name="message"
            className="Block"
          ></textarea>
          <input class="btn-send" type="submit" value="Enviar" />
        </form>
      </div>
    </div>
  );
}

export default Contact;
